/* Services section styling */

.services {
  text-align: left;
}

/* Improve main section title inside Services */
.services .section-title {
  text-align: center;
  margin-bottom: 1rem;
}

.services__grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}

/* New recurring/one-time layout */
.services-content h2 { margin-bottom: 0.5rem; }

/* Improve the includes block */
.services-intro {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  padding: 1rem 1.25rem;
  margin: 0.5rem 0 1rem;
}
.services-intro h3 {
  margin: 0 0 0.75rem;
  font-size: 1.1rem;
  color: var(--color-primary);
}
.includes-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 0.5rem 2rem;
}
.includes-list ul { padding-left: 0; margin: 0; }
.includes-list li {
  margin: 0.25rem 0;
  position: relative;
  padding-left: 1.25rem;
  color: var(--color-text);
}
.includes-list li::before {
  content: '✓';
  position: absolute;
  left: 0;
  top: 0;
  color: var(--color-primary);
  font-weight: 700;
}

/* Service tiles grid - equal height cards */
.services-hero-tiles {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
  align-items: stretch; /* stretch grid items */
  grid-auto-rows: 1fr; /* equal height rows */
}
.service-tile {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  height: 100%; /* fill grid track */
}
.service-tile h3 { margin: 0.25rem 0 0.25rem; }
.service-tile p { margin: 0; }
.service-tile .price { font-weight: 700; }
.service-tile .btn { margin-top: auto; }
.service-tile.popular { border-color: var(--color-primary); }
.service-icon { width: 40px; height: 40px; }
.price { font-weight: 700; }

.funnel-cta {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin: 1.25rem 0 0;
}
.funnel-cta p { margin: 0; }

/* FAQ */
.faq-list details {
  border: 1px solid var(--color-border);
  background: var(--color-surface);
  padding: 0.75rem 1rem;
  border-radius: var(--border-radius);
}
.faq-list details + details { margin-top: 0.5rem; }
.faq-list summary { cursor: pointer; font-weight: 600; }
.faq-list p { margin-top: 0.5rem; }

/* Modal */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: var(--color-overlay);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  z-index: 1000;
}
.modal {
  background: var(--color-surface);
  border-radius: var(--border-radius);
  max-width: 680px;
  width: 100%;
  box-shadow: 0 10px 30px var(--color-shadow);
}
.modal__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--color-border);
  padding: 0.75rem 1rem;
}
.modal__title { margin: 0; }
.modal__close {
  background: transparent;
  border: none;
  font-size: 1.5rem;
  line-height: 1;
  cursor: pointer;
}
.modal__body { padding: 1rem; }
.modal__footer {
  border-top: 1px solid var(--color-border);
  padding: 0.75rem 1rem;
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}
.service-card {
  background-color: var(--color-accent);
  border-radius: var(--border-radius);
  padding: 1.5rem;
  text-align: center;
  box-shadow: 0 2px 6px var(--color-shadow);
  transition: transform var(--transition-fast), box-shadow var(--transition-fast);
}

.service-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px var(--color-shadow);
}

.service-card__icon {
  width: 48px;
  height: 48px;
  margin-bottom: 1rem;
}

.service-card__name {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.service-card__description {
  font-size: 0.9rem;
  color: var(--color-text);
}